<template>
  <div>
    <div class="columns is-mobile is-centered mt-3 mb-3">
      <div class="column is-full hivid-maincol">
        <div class="card">
          <header class="card-header has-background-light">
            <p class="card-header-title is-centered">
              <span class="icon is-medium has-text-success">
                <i class="fab fa-2x fa-whatsapp"></i>
              </span>
            </p>
          </header>
          <progress class="progress is-small is-primary is-radiusless" max="100" v-if="loading">15%</progress>
          <div class="card-content">
            <div class="content">
              <p class="has-text-weight-semibold has-text-centered pb-3">{{ main_title }}</p>
              <form @submit="submitForm">
                <div class="field">
                  <div class="control">
                    <input class="input" type="text" v-model="phone" readonly>
                  </div>
                </div>
                <div class="field">
                  <div class="control">
                    <textarea class="textarea" v-model="default_message"></textarea>
                  </div>
                </div>
                <button type="submit" class="button is-success is-fullwidth has-text-weight-bold" v-if="!loading">{{ submit_btn_text }}</button>
              </form>
            </div>
          </div>
          <footer class="card-footer p-1 has-background-light">
            <span class="card-footer-item is-paddingless" v-for="(social, i) in socials" :key="i">
              <button :class="'button is-fullwidth '+social.color">
                <span class="icon"><i :class="social.icon"></i></span>
              </button>
            </span>
          </footer>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { parsePhoneNumber, isPossiblePhoneNumber, isValidPhoneNumber } from 'libphonenumber-js'

export default {
  name: 'HividWaform',
  props: {
    main_title: String,
    phone: String,
    default_message: String,
    submit_btn_text: String
  },
  data() {
    return {
      loading: false,
      defaultCountryCode: 'ID',
      socials: [{
          icon: 'fab fa-lg fa-whatsapp',
          color: 'hivid-wabg',
          share_url: 'https://tokoig.com',
        },
        {
          icon: 'fab fa-lg fa-facebook',
          color: 'hivid-fbbg',
          share_url: 'https://tokoig.com',
        },
        {
          icon: 'fab fa-lg fa-twitter',
          color: 'hivid-twbg',
          share_url: 'https://tokoig.com',
        },
        {
          icon: 'fab fa-lg fa-linkedin',
          color: 'hivid-libg',
          share_url: 'https://tokoig.com',
        },
        {
          icon: 'fab fa-lg fa-pinterest',
          color: 'hivid-pibg',
          share_url: 'https://tokoig.com',
        },
        {
          icon: 'fal fa-lg fa-envelope',
          color: 'hivid-embg',
          share_url: 'https://tokoig.com',
        },
      ],
    }
  },
  methods: {
    submitForm: function(e) {
      e.preventDefault()
      // Validate props value
      if (this.phone == null || this.default_message == null) {
        return alert('Widget not configured')
      }
      // Validate Phone
      if (!isPossiblePhoneNumber(this.phone, this.defaultCountryCode)) {
        return alert('Wrong Phone Number Format')
      }
      if (!isValidPhoneNumber(this.phone, this.defaultCountryCode)) {
        return alert('Phone Number Format Is Not Valid')
      }

      const phoneNumberResult = parsePhoneNumber(this.phone, this.defaultCountryCode)
      this.phone = phoneNumberResult.number

      let thisReplacer = this
      this.loading = true
      setTimeout(function() {
        thisReplacer.loading = false
        let phoneNumber = thisReplacer.phone.replace('+', '')
        let textMessage = encodeURIComponent(thisReplacer.default_message)
        window.open('https://wa.me/' + phoneNumber + '?text=' + textMessage, '_blank')
      }, 3000)
    }
  }
}
</script>
